import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import {
  Empty,
  Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  Table
  // message,
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import NumberFormat from "../../../App/NumberFormat";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/salesOrderDetailReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectSO = "/dashboard/sales/sales-orders";

const expandedRowRender = data => {
  const columns = [
    {
      title: "No",
      dataIndex: "index_no",
      width: "2%"
    },
    {
      title: getLanguage({ id: "items.code" }),
      dataIndex: "item_code",
      width: "15%"
    },
    {
      title: getLanguage({ id: "itemName" }),
      dataIndex: "item_name",
      width: "30%",
      render: text => {
        if (text && text.length > 50) {
          return (
            <Tooltip placement="bottom" title={text}>
              {text.substring(0, 50) + "..."}
            </Tooltip>
          );
        } else {
          return text;
        }
      }
    },
    {
      title: (
        <Tooltip placement="top" title={getLanguage({ id: "SOQty" })}>
          <a href="javascript:;">{getLanguage({ id: "SOQtys" })}</a>
        </Tooltip>
      ),
      dataIndex: "qty",
      width: "10%",
      align: "center",
      render: (text, row) => (
        <NumberFormat
          value={parseFloat(text)}
          suffix={" " + row.unit.unit_name}
        />
      )
    },
    {
      title: (
        <Tooltip
          placement="top"
          title={
            getLanguage({ id: "totalQtyDeliveredl" }) +
            " (" +
            getLanguage({ id: "inBaseUnitForm" }) +
            ")"
          }
        >
          <a href="javascript:;">{getLanguage({ id: "totalQtyDelivered" })}</a>
        </Tooltip>
      ),
      dataIndex: "delivered_qty", // dari DO
      width: "10%",
      align: "center",
      render: (text, row) => (
        <NumberFormat
          value={parseFloat(text)}
          suffix={" " + row.unit.unit_name}
        />
      )
      // render: (text, row) => {
      //   if (text && text.length > 7) {
      //     return (
      //       <Tooltip placement="bottom" title={text}>
      //         {text.substring(0, 7) + "..."}
      //       </Tooltip>
      //     );
      //   } else {
      //     return text + " " + row.unit.unit_name;
      //   }
      // }
    },
    {
      title: getLanguage({ id: "price" }) + " (IDR)",
      dataIndex: "item_price", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        return (
          <NumberFormat
            value={parseFloat(row.currency_rate) * parseFloat(text)}
          />
        );
      }
    },
    {
      title: getLanguage({ id: "discount" }) + " (IDR)",
      dataIndex: "sub_total_discount", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        return (
          <NumberFormat
            value={parseFloat(row.currency_rate) * parseFloat(text)}
          />
        );
      }
    },
    {
      title: getLanguage({ id: "sidebar.tax" }) + " (IDR)",
      dataIndex: "sub_total_tax", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        return (
          <NumberFormat
            value={parseFloat(row.currency_rate) * parseFloat(text)}
          />
        );
      }
    },
    {
      title: getLanguage({ id: "subTotal" }) + " (IDR)",
      dataIndex: "sub_total", // dari PR
      width: "10%",
      align: "right",
      render: (text, row) => {
        return (
          <NumberFormat
            value={parseFloat(row.currency_rate) * parseFloat(text)}
          />
        );
      }
    }
  ];

  const dataSource = data;
  return (
    <div>
      <strong>
        <IntlMessages id="purchaseInvoices.importPR.itemList" />
      </strong>

      <Table
        rowKey={record => record.index_no}
        size="small"
        columns={columns}
        dataSource={dataSource}
        pagination={false}
        style={{ marginBottom: "5px", marginTop: "5px" }}
      />
    </div>
  );
};

class SalesOrderDetailReport extends Component {
  state = {
    filter: {
      id_customer: null,
      id_location: null,
      transaction_status: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "transaction_date",
      sort_type: "asc",
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getSalesOrderDetailReports } = this.props;
    getSalesOrderDetailReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.salesOrderDetailReport.error) {
      notification("error", this.props.salesOrderDetailReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.salesOrderDetailReport.success) {
      notification("success", this.props.salesOrderDetailReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getSalesOrderDetailReports } = this.props;
    let filter = this.filterValue();

    getSalesOrderDetailReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getSalesOrderDetailReports } = this.props;
    this.setState({
      filter: {
        id_customer: null,
        id_location: null,
        transaction_status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getSalesOrderDetailReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.transaction_status) {
      string
        ? (string += "&transaction_status=" + filter.transaction_status)
        : (string += "transaction_status=" + filter.transaction_status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_customer) {
      string
        ? (string += "&id_customer=" + filter.id_customer)
        : (string += "id_customer=" + filter.id_customer);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getSalesOrderDetailReports } = this.props;
    //const { current_page } = this.props.salesOrderDetailReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getSalesOrderDetailReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getSalesOrderDetailReports } = this.props;
    this.setState({
      filter: {
        id_customer: null,
        id_location: null,
        transaction_status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getSalesOrderDetailReports();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateSalesOrderReport(id);
    } else {
      this.props.deactivateSalesOrderReport(id);
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getSalesOrderDetailReports } = this.props;
  //   const { search, filter } = this.props.salesOrderDetailReport;
  //   getSalesOrderDetailReports(page, search, filter);
  // };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "sales_order_no",
        rowKey: "sales_order_no",
        width: "16%",
        //fixed: "left",
        render: (text, row) => {
          let className;
          switch (row.transaction_status) {
            case 4:
              className = "new";
              break;
            case 6:
              className = "new";
              break;
            case 1:
              className = "ordered";
              break;
            case 2:
              className = "received";
              break;
            case 3:
              className = "closed";
              break;
            case 5:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>
                <Link to={`${redirectSO}/update/${row.id_sales_order}`}>
                  {text}
                </Link>
              </TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        data: true,
        rowKey: "transaction_date",
        width: "13%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "14%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "11%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalQty" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        width: "9.5%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalDiscount" }) + " (IDR)",
        dataIndex: "total_discount",
        rowKey: "total_discount",
        width: "10%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalTax" }) + " (IDR)",
        dataIndex: "total_tax",
        rowKey: "total_tax",
        width: "9.5%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "total" }) + " (IDR)",
        dataIndex: "total_amount",
        rowKey: "total_amount",
        width: "10%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status",
        rowKey: "status",
        width: "9%",
        align: "center",
        render: (text, row) => {
          let className;
          let display;
          switch (row.transaction_status) {
            case 4:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 1:
              className = "ordered";
              display = getLanguage({ id: "ordered" });
              break;
            case 2:
              className = "received";
              display = getLanguage({ id: "delivered" });
              break;
            case 3:
              className = "closed";
              display = getLanguage({ id: "closed" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "cancelled" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    const excelColumns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "sales_order_no",
        rowKey: "sales_order_no",
        width: "16%"
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        date: true,
        rowKey: "transaction_date",
        width: "13%"
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name"
      },
      {
        title: getLanguage({ id: "sidebar.customer" }),
        dataIndex: "customer_name",
        rowKey: "customer_name",
        width: "11%"
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalQty" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        width: "9.5%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalDiscount" }) + " (IDR)",
        dataIndex: "total_discount",
        rowKey: "total_discount",
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "purchaseOrders.totalTax" }) + " (IDR)",
        dataIndex: "total_tax",
        rowKey: "total_tax",
        width: "9.5%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "total" }) + " (IDR)",
        dataIndex: "total_amount",
        rowKey: "total_amount",
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "transaction_status_string",
        rowKey: "transaction_status_string",
        width: "9%",
        align: "center"
      },
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        width: "15%"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        width: "30%"
      },
      {
        title: getLanguage({ id: "SOQty" }),
        dataIndex: "qty",
        width: "10%",
        align: "center",
        parse: true
      },
      {
        title: getLanguage({ id: "SOUnit" }),
        dataIndex: "unit.unit_name",
        width: "10%",
        align: "center"
      },
      {
        title:
          getLanguage({ id: "totalQtyDeliveredl" }) +
          " (" +
          getLanguage({ id: "inBaseUnitForm" }) +
          ")",
        dataIndex: "delivered_qty", // dari DO
        width: "10%",
        align: "center",
        parse: true
      },
      {
        title: getLanguage({ id: "items.baseUnit" }),
        dataIndex: "unit.unit_name", // dari DO
        width: "10%",
        align: "center"
      },
      {
        title: getLanguage({ id: "price" }) + " (IDR)",
        dataIndex: "item_price", // dari PR
        width: "10%",
        parse: true
      },
      {
        title: getLanguage({ id: "discount" }) + " (IDR)",
        dataIndex: "sub_total_discount", // dari PR
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "sidebar.tax" }) + " (IDR)",
        dataIndex: "sub_total_tax", // dari PR
        width: "10%",
        align: "right",
        parse: true
      },
      {
        title: getLanguage({ id: "subTotal" }) + " (IDR)",
        dataIndex: "sub_total", // dari PR
        width: "10%",
        align: "right",
        parse: true
      }
    ];

    // Data setup
    const { salesOrderDetailReport } = this.props;
    const {
      sales_order_detail_reports,
      locations,
      customers,
      filter,
      filter_select
    } = salesOrderDetailReport;

    const dataSource = [];
    let expandedRowKeys = [];
    let excelDataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    if (sales_order_detail_reports && sales_order_detail_reports.length > 0) {
      // for table and print's datasource
      Object.keys(sales_order_detail_reports).map((item, index) => {
        sales_order_detail_reports[index].sales_order_details.map(data => {
          data.currency_rate = sales_order_detail_reports[index].currency_rate;
          data.received_qty = data.received_qty || "0.00";
        });
        return dataSource.push({
          ...sales_order_detail_reports[index],
          key: index
        });
      });

      // for print's expandedRow
      Object.keys(sales_order_detail_reports).map((item, index) => {
        return expandedRowKeys.push(index);
      });

      // for excel export
      sales_order_detail_reports.map((item, index) => {
        let transaction_status;
        switch (item.transaction_status) {
          case 4:
            transaction_status = getLanguage({ id: "new" });
            break;
          case 1:
            transaction_status = getLanguage({ id: "ordered" });
            break;
          case 2:
            transaction_status = getLanguage({ id: "delivered" });
            break;
          case 3:
            transaction_status = getLanguage({ id: "closed" });
            break;
          case 5:
            transaction_status = getLanguage({ id: "cancelled" });
            break;
          default:
            transaction_status = getLanguage({ id: "new" });
            break;
        }
        item.total_discount =
          parseFloat(item.currency_rate) * parseFloat(item.total_discount);
        item.total_tax =
          parseFloat(item.currency_rate) * parseFloat(item.total_tax);
        item.total_amount =
          parseFloat(item.currency_rate) * parseFloat(item.total_amount);
        item.sales_order_details.map((data, i) => {
          data.item_price =
            parseFloat(item.currency_rate) * parseFloat(data.item_price);
          data.sub_total_discount =
            parseFloat(item.currency_rate) *
            parseFloat(data.sub_total_discount);
          data.sub_total_tax =
            parseFloat(item.currency_rate) * parseFloat(data.sub_total_tax);
          data.sub_total =
            parseFloat(item.currency_rate) * parseFloat(data.sub_total);

          excelDataSource.push({
            ...item,
            transaction_status_string: transaction_status,
            ...data
          });
        });
      });
    }
    const { loading, loadingForeign } = this.props.salesOrderDetailReport;

    const fileName = getLanguage({ id: "salesOrderDetailReports.pageHeader" });
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="salesOrderDetailReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="salesOrderDetailReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.sales_order_details)
              }
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              defaultExpandAllRows={true}
              expandedRowKeys={expandedRowKeys}
              expandedRowRender={record =>
                expandedRowRender(record.sales_order_details)
              }
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin=".6cm"
              scale={0.51}
              title="salesOrderDetailReports.pageHeader"
            />

            <ExcelWrapper
              title="salesOrderDetailReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={excelColumns}
              data={excelDataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "transactionNo" }) +
                ", " +
                getLanguage({ id: "createdBy" }) +
                ", " +
                getLanguage({ id: "orderedBy" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                disabled={
                  !permissions.includes("Sales Order Detail Report")
                    ? true
                    : false
                }
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="customers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_customer"
                      )}
                      value={
                        this.state.filter.id_customer
                          ? this.state.filter.id_customer
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.customer" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {customers.map((data, key) => (
                        <Option key={key} value={data.id_customer}>
                          {`${data.customer_code} (${data.customer_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="transaction_date">
                            <IntlMessages id="salesOrderReports.filter.date" />
                          </Option>
                          <Option key={2} value="sales_order_no">
                            <IntlMessages id="salesOrders.salesOrderNo" />
                          </Option>
                          <Option key={3} value="location_name">
                            <IntlMessages id="sidebar.location" />
                          </Option>
                          <Option key={4} value="customer_name">
                            <IntlMessages id="sidebar.customer" />
                          </Option>
                          <Option key={5} value="total_amount">
                            <IntlMessages id="totalAmount" />
                          </Option>
                          <Option key={6} value="total_qty">
                            <IntlMessages id="totalQuantity" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "100%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col span={8}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "transaction_status"
                          )}
                          value={
                            this.state.filter.transaction_status
                              ? this.state.filter.transaction_status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder={getLanguage({ id: "status" })}
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={4}>
                            {getLanguage({ id: "new" })}
                          </Option>
                          <Option value={1}>
                            {getLanguage({ id: "ordered" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "received" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "closed" })}
                          </Option>
                          <Option value={5}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>
                      {/* <Col span={8}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : page_size
                          }
                          style={{ width: "80%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col> */}
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          disabled={
                            !permissions.includes("Sales Order Detail Report")
                              ? true
                              : false
                          }
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : sales_order_detail_reports.length === 0 &&
              !this.props.salesOrderDetailReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="salesOrderDetailReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    bordered={true}
                    className="invoiceListTable"
                    columns={columns}
                    dataSource={dataSource}
                    defaultExpandAllRows={true}
                    expandedRowRender={record =>
                      expandedRowRender(record.sales_order_details)
                    }
                    loading={loading}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    salesOrderDetailReport: state.salesOrderDetailReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  SalesOrderDetailReport
);
